.marker{
    width: 48px;
    border: 1px solid #999;
    background-color: white;
    border-radius: 2px;
    text-align: center;
    padding: 4px 0;
    font-size: 18px;
}

.marker.selected{
    background-color: yellow;
    border-color: black;
 }


